@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;600;700&display=swap');
html
{
    height: 100%;
    overflow-x: hidden;
}
.Intro
{
    background-image: url(../img/IntroImg.svg);
    background-color: #cccccc;
    width: 100vw;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.about1
{
    background-image: url(../img/background2.svg);
    background-color: #cccccc;
    width: 100vw;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding-top: 60px;
    padding-bottom: 60px;
}
.pwhite
{
    color: white;
    text-align: center;
    margin-right: 30%;
    margin-left: 30%;
    font-size: 20px;
}
.eye
{
    display: block;
  margin-left: auto;
  margin-right: auto;
  padding-top: 8%;
  padding-bottom: 5%;
  height: 170px;
}
h2, h1
{
    font-family: 'Josefin Sans', sans-serif;
    text-align: center;
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
}
h2
{
    font-size: 40px;
    color: #FFBC21;
}
h2.title
{

    margin-bottom: 30px;
}
.title2
{
    padding-top: 40px;
    margin: 0;
    color: black;
}
h1
{
    font-size: 50px;
    color: white;
}

.LearnMore
{
    font-family: 'Josefin Sans', sans-serif;
    position: absolute;
    top: 75%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
    background-color: white;
    border-radius: 100px;
    border: none;
    font-size: 30px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 40px;
    padding-left: 40px;
    text-decoration: none;
    color: black;
    box-shadow:0px 3px 6px rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.contain
{
    background-color: white;
    width: 60%;
    margin: auto;
    padding-bottom: 30px;
}
.backimg
{
    background-attachment: fixed;
}
.pcontainer
{
    margin: 0;
    padding-bottom: 40px;
    padding-right: 60px;
    padding-left: 60px;
}
.pcontainer:nth-child(1)
{
    padding-top: 50px;
}
.pcontainer:nth-child(4)
{
    padding-bottom: 20px;
}
.purple
{
    color: #A44FC2;
}
#checkitout
{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 22px;
    font-weight: normal;
    text-align: center;
    margin: 0;
    padding-bottom: 20px;
}
#Manibtn
{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 20px;
   background: linear-gradient(120deg, #BE76F5 , #DC6262, #BE76F5);
   background-size: 200% 200%;
   background-position: top left;
   color: white;
   border-radius: 100px;
   text-decoration: none;
   padding: 7px 20px;
   box-shadow: 0 3px 6px rgba(0,0,0,.2);
    
}   
#Manibtn:hover
{
    background-position: bottom right;
    animation: btn .3s ease-out;
}

@keyframes btn {
    from {background-position: top left;}
    to{background-position: bottom right;}
}


.center
{
    text-align: center;
    margin-bottom: 20px;
}
table
{
    width: 70%;
    margin:auto;
}
tr, td
{
    width: 33.3%;
}
.social
{
    display: flex;
  justify-content: center;
  height: 40px;
}
.footsocial
{
    position: relative;
    top: 8px;
    float: right;
    height: 30px;
    margin-right: 5px;
    margin-left: 5px;
}

.container
{
    background-color: white;
    border-radius: 40px;
    width: 250px;
    margin: auto;
    height: 400px ;
}
.container:nth-child(1)
{
    margin-left:30px;
}
.container:nth-last-child(1)
{
    margin-right:50px;
}
.pfp
{
    position: relative;
    top: -30px;
    z-index: 1;
    background-color: grey;
    border-radius: 50% 50%;
    height: 150px;
    width: 150px;
    margin: auto;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
}
h5
{
    position: relative;
    top:-10px;
    text-align: center;
    margin: auto;
    margin-bottom: 20px;
    
}
.grid
{
    width: 100%;
    overflow: auto;
    padding-top: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 15px;
    margin-right: 30px;
    cursor: grab;
}
.count
{
    font-size: 100px;
    color: #BE76F5;
}
.rules
{
    margin-left: 155px;
    margin-right: 90px;
}
h3{
    font-family: 'Josefin Sans', sans-serif;
    margin-left: 70px;
}

#Footer
{
    height: 80px;
    padding: 30px;
}
#footname
{
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    margin:auto;
    font-family: 'Josefin Sans', sans-serif;
    color: white;
}